<template>
  <v-app light>
    <v-content>
      <v-container fill-height fluid>
        <v-layout align-center justify-center>
          <v-flex md4 sm8 xs12>

            <v-card class="elevation-12">
              <v-toolbar color="grey darken-1" dark>
                <v-toolbar-title> 登入</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>

              <v-form action="" method="post">
                <input name="_xsrf" type="hidden" v-model="x"/>
                <v-card-text>
                  <v-text-field
                          color="brown lighten-1"
                          label="请输入用户名"
                          name="username"
                          prepend-icon="person"
                          type="text"
                          v-model="usr"
                  ></v-text-field>
                  <v-text-field
                          color="brown lighten-1"
                          label="请输入密码"
                          name="password"
                          prepend-icon="lock"
                          type="password"
                          v-model="pwd"
                  ></v-text-field>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn @keyup.enter="submit" color="grey lighten-2" tabindex="0"
                         type="submit"> Go
                  </v-btn>
                </v-card-actions>
              </v-form>
            </v-card>

          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  export default {
    name: 'Auth',
    data() {
      return {
        x: "",
        usr: "",
        pwd: "",
      }
    },
    mounted() {
      let vm = this
      let r = document.cookie.match("\\b" + "_xsrf" + "=([^;]*)\\b")
      vm.$data.x = r ? r[1] : ''
    },
  }
</script>
